import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import {message, Input, Tooltip} from 'antd';
import {Utils, numeral, checkTime, PANCAKE_LAUNCH_TIME, STAKING_TIME} from '../../utils/Utils';
import Countdown  from "../../component/Countdown/Countdown";
import {CopyToClipboard} from 'react-copy-to-clipboard';
import {CopyOutlined, LoadingOutlined, QuestionCircleOutlined} from "@ant-design/icons";
import './Staking.scss';

class Staking extends Component {
  _isMounted = false;
  constructor(props){
    super(props);
    this.state={
      stakeInput:'',
      unstakeInput:''
    }
  }

  componentDidMount = () =>{
    this._isMounted = true;
  }

  componentWillUnmount = () =>{
    this._isMounted = false;
  }

  render() {
    return (
      <div className="staking" style={{width: this.props.size.width, height: this.props.size.height}}>
        <div className="totalMinerWrap">
          <img draggable="false" src={require("../../assets/images/totalMinerWrap.png")} alt="totalMinerWrap"/>
          <div className="contentWrap" align="left">
            <h2>{this.props.languageFile.home.totalMiner}</h2>
            <p>{numeral(this.props.state.totalMiners).format('0,0')}</p>
          </div>
        </div>
        <img draggable="false" className="textLogo" src={require("../../assets/images/textLogo.png")} alt="textLogo"/>

        <div className="walletBalanceWrap">
          <img draggable="false" src={require("../../assets/images/walletBalanceWrap.png")} alt="walletBalanceWrap"/>
          <div className="contentWrap" align="left">
            <h2>{this.props.languageFile.home.balance}</h2>
            <p>{numeral(this.props.state.nativeBalance).format('0,0.[000]')} ETH</p>
          </div>
        </div>

        <div className="fundWrap">
          <img draggable="false" src={require("../../assets/images/staking/fundWrap.png")} alt="fundWrap"/>
          <div className="contentWrap" align="middle">
            <h1>{this.props.languageFile.staking.motherFund}</h1>
            <h2>
              {/* {this.props.languageFile.message.comingSoon} */}
              {numeral(this.props.state.totalFundAmount).format('0,0.[0000]')} ETH
            </h2>
            <p>{numeral(this.props.state.dividendsRate).format('0,0')}% {this.props.languageFile.staking.divRules}</p>
          </div>
        </div>

        <div className="socialMediaWrap">
          <a href={this.props.lang === "en" ? "https://t.me/BabyMiner" : "https://t.me/BabyMinercn"} target="_blank" rel="noopener noreferrer">
            <img draggable="false" src={require("../../assets/images/telegram.png")} alt="telegram"/>
          </a>
          <a href={"https://twitter.com/babyminercoin"} target="_blank" rel="noopener noreferrer">
            <img draggable="false" src={require("../../assets/images/twitter.png")} alt="twitter"/>
          </a>
          <a href={"https://bscscan.com/token/0xB8328dE7A7225F56d5e1cEB55AAA7965d403b2af"} target="_blank" rel="noopener noreferrer">
            <img draggable="false" src={require("../../assets/images/bscscan.svg")} alt="bscscan" style={{background:"#ffffff"}}/>
          </a>

          <div className="langToggle" onClick={this.props.changLang}>
            <span className={this.props.lang === 'en' ? 'selected' : ''}>
              EN
            </span>
            &nbsp;/ &nbsp;
            <span className={this.props.lang === 'zh_CN' ? 'selected' : ''}>
              CN
            </span>
          </div>
        </div>


        <div className="dividendsWrap">
          <img draggable="false" src={require("../../assets/images/staking/dividendsWrap.png")} alt="dividendsWrap"/>
          <img className="dividendsWrapMobile" draggable="false" src={require("../../assets/images/staking/dividendsWrapMobile.png")} alt="dividendsWrapMobile"/>

          <div className="contentWrap" align="middle">
            <Tooltip title={this.props.languageFile.staking.stakeNote}>
              <h3>
                {this.props.languageFile.staking.round}&nbsp;
                {this.props.state.dividendsRound}&nbsp;
                {this.props.languageFile.staking.roundEnd}&nbsp;
                {this.props.languageFile.staking.divPool}&nbsp;
                <QuestionCircleOutlined/>
              </h3>
            </Tooltip>



            <h1>
              {/* {this.props.languageFile.message.comingSoon} */}
              {numeral(this.props.state.curDivAmount).format('0,0.[0000]')} ETH
            </h1>


            <div className="dataWrap" align="left">
              <p>
                {this.props.languageFile.staking.totalStaked}:
                <span className="floatRight">{numeral(this.props.state.totalStaked).format('0,0')}</span>
              </p>
              <p>
                {this.props.languageFile.staking.yourEstimate}:
                <span className="floatRight">{numeral(this.props.state.yourEstimate).format('0,0.[0000]')} ETH</span>
              </p>
              <p>{this.props.languageFile.staking.apy}:
                <span className="floatRight">{numeral(this.props.state.stakingApy).format('0,0.[00]')}%</span>
              </p>
              {
                checkTime(STAKING_TIME)
                  ?
                    <p>
                      {this.props.languageFile.staking.divCountdown}&nbsp;
                      <span className="floatRight"><Countdown date={this.props.state.divCountdown}/></span>
                    </p>
                  :
                  <p>
                    {this.props.languageFile.staking.startCountdown}&nbsp;
                    <span className="floatRight"><Countdown date={STAKING_TIME}/></span>
                  </p>

              }

            </div>

          </div>
        </div>

        <div className="stakeUnstakeWrap">
          <img draggable="false"  src={require("../../assets/images/staking/stakeUnstakeWrap.png")} alt="stakeUnstakeWrap"/>
          <div className="contentWrap" align="left">
            <div className="topWrap" align="left">
              <Tooltip title={this.props.languageFile.staking.stakeNote}>
                <h1>
                  {this.props.languageFile.staking.stake}&nbsp;
                  <QuestionCircleOutlined style={{color:'black'}}/>
                </h1>
              </Tooltip>


              <Input
                suffix={
                  <span className="suffix">BBM</span>
                }
                value={this.state.stakeInput}
                defaultValue={'0.00'}
                onChange={(e)=>this.onChangeStakeValue(e)}
              />
              <p onClick={()=>{this.maxStakeInput()}}>{this.props.languageFile.staking.available} BBM:  {numeral(this.props.state.myTokenBalance).format('0,0')}</p>

              {
                this.props.state.stakingAllowance > 0
                  ?
                    <div
                      className="button"
                      onClick={()=>{
                        this.stake();
                      }}>
                      {
                        this.state.stakeLoading
                          ? <LoadingOutlined/>
                          : this.props.languageFile.staking.stake
                      }
                    </div>
                  :

                checkTime(STAKING_TIME)
                  ?
                    <div
                      className="button"
                      onClick={()=>{
                        this.approve();
                      }}>
                      {
                        this.state.approveLoading
                          ? <LoadingOutlined/>
                          : this.props.languageFile.staking.approve
                      }
                    </div>
                  :
                  <div className="button">
                    <Countdown date={STAKING_TIME}/>
                  </div>

              }

            </div>

            <div className="bottomWrap" align="left">
              <Tooltip title={this.props.languageFile.staking.unstakeNote}>
                <h1>
                  {this.props.languageFile.staking.unstake}&nbsp;
                  <QuestionCircleOutlined style={{color:'black'}}/>
                </h1>
              </Tooltip>

              <Input
                suffix={
                  <span className="suffix">BBM</span>
                }
                value={this.state.unstakeInput}
                onChange={(e)=>this.onChangeUnStakeValue(e)}
              />
              <p onClick={()=>{this.maxUnstakeInput()}}>BBM {this.props.languageFile.staking.staked}:  {numeral(this.props.state.staked).format('0,0')}</p>

              <div
                className="button"
                onClick={()=>{
                  this.unstake();
                }}>
                {
                  this.state.unstakeLoading
                    ? <LoadingOutlined/>
                    : this.props.languageFile.staking.unstake
                }
              </div>
            </div>
          </div>
        </div>

        <div className="claimWrap">
          <img draggable="false"  src={require("../../assets/images/staking/claimWrap.png")} alt="claimWrap"/>
          <div className="contentWrap" align="left">

            <p>
              {this.props.languageFile.staking.pending}:
              <span className="floatRight">{numeral(this.props.state.pending).format('0,0')} BBM</span>
            </p>

            <p className="countdown">
              {this.props.languageFile.staking.pendingCountdown}:
              <span className="floatRight">
                <Countdown date={this.props.state.pendingCountdown}/>
              </span>
            </p>

            <p className="unstaked">
              {this.props.languageFile.staking.unstaked}:
              <span className="floatRight">{numeral(this.props.state.unstaked).format('0,0')} BBM</span>
            </p>

            <div
              className="button"
              onClick={()=>{
                this.withdraw();
              }}>
              {
                this.state.withdrawLoading
                  ? <LoadingOutlined/>
                  : this.props.languageFile.staking.claim
              }
            </div>


            <p className="divClaim">
              {this.props.languageFile.staking.earned}:&nbsp;
              <span>{numeral(this.props.state.stakingEarned).format('0,0.[0000]')} ETH</span>

              <span
                className="button"
                style={{color: 'white', fontWeight:"normal"}}
                onClick={()=>{
                  this.claimDividends();
                }}>
                {
                  this.state.claimDividendsLoading
                    ? <LoadingOutlined style={{color: 'white'}}/>
                    : this.props.languageFile.staking.claim
                }
              </span>

            </p>

          </div>
        </div>
        <Link to='/'>
          <div className="referralRulesWrap miningRulesWrap button" onClick={this.toggleMiningRule}>
            <img draggable="false" src={require("../../assets/images/referralRulesWrap.png")} alt="referralRulesWrap"/>
            <div className="contentWrap" align="center">
              {this.props.languageFile.staking.backToHome}
            </div>
          </div>
        </Link>

        {
          checkTime(PANCAKE_LAUNCH_TIME)
            ?
              <div className="pancakeCountdownWrap pancakeCountdownWrapOut">
                <CopyToClipboard text={Utils.tokenAddress}
                  onCopy={() => {message.info(this.props.languageFile.message.successCopy, 1)}}>
                  <h2 className="addressInfo">
                    <span>{this.props.languageFile.home.contract}: </span>
                    <span className="address">{Utils.tokenAddress}</span>
                    <span><CopyOutlined /></span>
                  </h2>
                </CopyToClipboard>
                <div className="buttonGroups">
                  <a href={`https://pancakeswap.finance/swap?inputCurrency=ETH&outputCurrency=${Utils.tokenAddress}`} target="_blank" rel="noopener noreferrer">
                    <img src={require("../../assets/images/cake.png")} alt="trade"/>
                    {this.props.languageFile.home.trade}
                  </a>
                  <a href="https://www.dextools.io/app/pancakeswap/pair-explorer/0x915c6853a7edb119b211e4a613c3337559f74439" target="_blank" rel="noopener noreferrer">
                    <img src={require("../../assets/images/dext.png")} alt="dext"/>
                    {this.props.languageFile.home.chart}
                  </a>
                </div>

              </div>
            :
            <div className="pancakeCountdownWrap">
              <h2>{this.props.languageFile.home.swapCountdown} <Countdown date={PANCAKE_LAUNCH_TIME}/></h2>
              <CopyToClipboard text={Utils.tokenAddress}
                onCopy={() => {message.info(this.props.languageFile.message.successCopy, 1)}}>
                <h2 className="addressInfo">
                  <span>{this.props.languageFile.home.contract}: </span>
                  <span className="address">{Utils.tokenAddress}</span>
                  <span><CopyOutlined /></span>
                </h2>
              </CopyToClipboard>
            </div>
        }
      </div>
    );
  }

  maxStakeInput = () =>{
    this.setState({
      stakeInput: this.props.state.myTokenBalance
    })
  }

  maxUnstakeInput = () =>{
    this.setState({
      unstakeInput: this.props.state.staked
    })
  }

  onChangeStakeValue=(e)=>{
    if(isNaN(e.target.value)){
      return;
    }
    this.setState({
      stakeInput:e.target.value
    })
  }

  onChangeUnStakeValue=(e)=>{
    if(isNaN(e.target.value)){
      return;
    }
    this.setState({
      unstakeInput:e.target.value
    })
  }

  checkGas = () =>{

    // if(this.props.state.nativeBalance < 0.09) {
    //   message.info(this.props.languageFile.message.insufficientGas, 3);
    //   return false;
    // }
    return true;
  }

  approve = () =>{
    if(this.state.approveLoading ){
      return;
    }
    if(!this._isMounted){
      return;
    }
    this.setState({
      approveLoading: true
    })

    if(Utils.mainToken && Utils.mainToken.methods){
      Utils.mainToken.methods.approve(Utils.babyStaking._address, "0xFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFFF").send({
        from:this.props.address
      })
      .on('transactionHash', (hash)=>{
        console.log(hash);
        if(!this._isMounted){
          return;
        }
        this.setState({
          approveLoading: true
        })
        message.info(this.props.languageFile.message.transactionSent,3);
      })
      .then(res => {
        if(!this._isMounted){
          return;
        }
        this.setState({
          approveLoading: true
        })
        this.props.getStakingData();
        message.info(this.props.languageFile.message.transactionSuccess, 3)
      })
      .catch(err => {
        console.log(err)
        if(!this._isMounted){
          return;
        }
        this.setState({
          approveLoading: false
        })
      })
    }
    else{
      if(!this._isMounted){
        return;
      }
      this.setState({
        approveLoading: false
      })
    }
  }


  stake = () => {
    if(!checkTime(this.props.state.stakingStarttime) || this.state.withdrawLoading){
      return;
    }
    if(parseFloat(this.state.myTokenBalance) < this.state.stakeInput || isNaN(parseFloat(this.state.stakeInput)) || parseFloat(this.state.stakeInput) <= 0){
      // message.info(this.props.languageFile.message.insufficientFunds, 3);
      return;
    }
    let stakeAmount = Utils.web3.utils.toWei(this.state.stakeInput.toString());

    if(!this._isMounted){
      return;
    }
    this.setState({
      stakeLoading: true
    })

    if(Utils.babyStaking && Utils.babyStaking.methods){
      Utils.babyStaking.methods.stake(stakeAmount).send({
        from:this.props.address
      })
      .on('transactionHash', (hash)=>{
        console.log(hash);
        message.info(this.props.languageFile.message.transactionSent,3);
      })
	    .then(res => {
        this.setState({
          stakeLoading:false
        })
        this.props.getStakingData();
        message.info(this.props.languageFile.message.transactionSuccess, 3)
	    })
	    .catch(err => {
        console.log(err)
        if(!this._isMounted){
          return;
        }
        this.setState({
          stakeLoading: false
        })
      })

    }
    else{
      if(!this._isMounted){
        return;
      }
      this.setState({
        stakeLoading: false
      })
    }
  }

  unstake = () => {
    if(!checkTime(this.props.state.stakingStarttime) || this.state.withdrawLoading){
      return;
    }
    if(parseFloat(this.state.myTokenBalance) < this.state.unstakeInput || isNaN(parseFloat(this.state.unstakeInput)) || parseFloat(this.state.unstakeInput) <= 0){
      // message.info(this.props.languageFile.message.insufficientFunds, 3);
      return;
    }
    let unstakeAmount = Utils.web3.utils.toWei(this.state.unstakeInput.toString());

    if(!this._isMounted){
      return;
    }
    this.setState({
      unstakeLoading: true
    })

    if(Utils.babyStaking && Utils.babyStaking.methods){
      Utils.babyStaking.methods.unstake(unstakeAmount).send({
        from:this.props.address
      })
      .on('transactionHash', (hash)=>{
        console.log(hash);
        message.info(this.props.languageFile.message.transactionSent,3);
      })
	    .then(res => {
        this.setState({
          unstakeLoading:false
        })
        this.props.getStakingData();
        message.info(this.props.languageFile.message.transactionSuccess, 3)
	    })
	    .catch(err => {
        console.log(err)
        if(!this._isMounted){
          return;
        }
        this.setState({
          unstakeLoading: false
        })
      })

    }
    else{
      if(!this._isMounted){
        return;
      }
      this.setState({
        unstakeLoading: false
      })
    }
  }

  claimDividends = () => {
    if(!checkTime(this.props.state.stakingStarttime) || this.state.withdrawLoading || parseFloat(this.props.state.stakingEarned) === 0){
      return;
    }

    if(!this._isMounted){
      return;
    }
    this.setState({
      claimDividendsLoading: true
    })

    if(Utils.babyStaking && Utils.babyStaking.methods){
      Utils.babyStaking.methods.getReward().send({
        from:this.props.address
      })
      .on('transactionHash', (hash)=>{
        console.log(hash);
        message.info(this.props.languageFile.message.transactionSent,3);
      })
	    .then(res => {
        this.setState({
          claimDividendsLoading:false
        })
        this.props.getStakingData();
        message.info(this.props.languageFile.message.transactionSuccess, 3)
	    })
	    .catch(err => {
        console.log(err)
        if(!this._isMounted){
          return;
        }
        this.setState({
          claimDividendsLoading: false
        })
      })

    }
    else{
      if(!this._isMounted){
        return;
      }
      this.setState({
        claimDividendsLoading: false
      })
    }
  }

  withdraw = () => {
    if(!checkTime(this.props.state.stakingStarttime) || this.state.withdrawLoading || parseFloat(this.props.state.unstaked) === 0){
      return;
    }

    if(!this._isMounted){
      return;
    }
    this.setState({
      withdrawLoading: true
    })
    console.log();
    if(Utils.babyStaking && Utils.babyStaking.methods){
      Utils.babyStaking.methods.withdraw(Utils.web3.utils.toWei(this.props.state.unstaked.toString())).send({
        from:this.props.address
      })
      .on('transactionHash', (hash)=>{
        console.log(hash);
        message.info(this.props.languageFile.message.transactionSent,3);
      })
	    .then(res => {
        this.setState({
          withdrawLoading:false
        })
        this.props.getStakingData();
        message.info(this.props.languageFile.message.transactionSuccess, 3)
	    })
	    .catch(err => {
        console.log(err)
        if(!this._isMounted){
          return;
        }
        this.setState({
          withdrawLoading: false
        })
      })

    }
    else{
      if(!this._isMounted){
        return;
      }
      this.setState({
        withdrawLoading: false
      })
    }
  }
}

export default Staking;
